  <div id="content">
    <h1>{{ post.title }}</h1>

    <p class="posttime">@{{ post.nickname }} ({{ post.addr | formatAddr }})　
      <span style="margin-left: 1rem">{{ post.created  | formatTime}}</span></p>

    <mavon-editor
     :value="post.body + '<br>' + '<br>' + keywords + post.json_metadata.keywords.join('　')"
    :subfield = "prop.subfield"
    :defaultOpen = "prop.defaultOpen"
    :toolbarsFlag = "prop.toolbarsFlag"
    :editable="prop.editable"
    :scrollStyle="prop.scrollStyle"
    ></mavon-editor>

    <!--<br>-->


