<script>

  import OtherReply from './OtherReply'
    export default {
      name: "Trend",
      data() {
        return {
          db:{},
          post: {},
          data:'',
          flag:localStorage.getItem(this.$route.params.permlink),
          replyFlag: false,
          text:'',
          areply:{},
          keywords: '关键词：　'
        }
      },

      methods:{
        addCollection() {
          let db = this.$store.state.db
          const collection = this.$store.state.username + 'collection'
          // console.log(888, db, '版本号', db.version)

          //得到操作 'collection' 表的事务对象
          let transaction = db.transaction([collection], 'readwrite') //可读可写事务

          transaction.oncomplete = function (event) {
            console.log("事务操作完成")
          }
          transaction.onerror = function (event) {
            console.log("事务操作出错")
            console.dir(event)
          }
          transaction.onsuccess = function (event) {
            console.log('事务操作成功')
          }

          //从事务对象里得到表里的objectStore对象
          let objectStore = transaction.objectStore(collection)
          let post = this.post
          objectStore.add(post)
          // console.log(456, "收藏成功")

          //设置收藏标记
          let collectionFlag = this.$route.params.permlink
          localStorage.setItem(collectionFlag, 'steemjiang')
          this.flag = localStorage.getItem(this.$route.params.permlink)

        },

        reply(){
          this.replyFlag = true
        },

        replypost(){
          this.replyFlag = false
          let wif = this.$store.state.password
          let parentAuthor = this.$route.params.author
          let parentPermlink = this.$route.params.permlink
          let author = this.$store.state.username
          let permlink = this.getstr()
          let title = ''
          let body = this.text
          let tags = ['cn', 'steemjiang']
          let jsonMetadata = {"tags": tags, "app": "steemit/1.0", "format": "markdown"}

          //发布到steem上
          this.postToSteem(wif, parentAuthor, parentPermlink, author, permlink, title, body, jsonMetadata)

          //把数据拼成一个JSON直接显示在前端
          let reply = {
            "parent_author": parentAuthor,
            "parent_permlink": parentPermlink,
            "title": title,
            "author": author,
            "permlink": permlink,
            "body": body,
            "created": new Date().toISOString().split('.')[0],
            "json_metadata": JSON.stringify(jsonMetadata)
          }
          this.areply = reply
          this.text = ''
        },

        clear(){
          this.replyFlag = false
          this.text = ''
        },

        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }

      },

      watch:{
          flag(newVal){
            return newVal
          }
      },

      computed: {
        prop () {
          let data = {
            subfield: false,
            defaultOpen: 'preview',
            editable: false,
            toolbarsFlag: false,
            scrollStyle: true
          }
          return data
        },

      },

      mounted() {
        //如果vuex中有值，则直接取出，否则从steem网络中取
        if(this.$store.state.hot != false){
          let posts = this.$store.state.hot
          posts.forEach(item => {
            if(item.permlink == this.$route.params.permlink){
              this.post = item
              // console.log(622, '找到啦')
            }
          })
        }

        // //如果数据库没打开，则打开
        // if(this.$store.state.posts == false){
        //   let _this = this
        //   // console.log(433, 'db is null')
        //   async function main() {
        //     let db = await _this.initDatabase()
        //     _this.$store.commit('saveDB',db)
        //     let posts = await _this.getAllPosts(db)
        //     _this.$store.commit('savePosts', posts.reverse())
        //   }
        //   main()
        // }



      },

      filters:{
        formatTime(data){
          return data.substring(0,10)
        },
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },
      },

      components: {
        OtherReply
      }
    }
</script>

