    padding-bottom: 3rem;
   }
  .replymd{
    min-height: auto;
    box-shadow: transparent;
    margin-bottom: 0.5rem;
  }

  #main{
    width: 95%;
    height: 20rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .child{
    margin-left: 2rem;
  }

  .secchild{
    margin-left: 4rem;
  }
  img {
    vertical-align: middle;
    width: 2rem !important;
