<template>
  <div class="login">
    <h2>钱包</h2>
    <div v-if="flag">
      <!--代币：LARK <br>-->
      <!--总量： {{ totalSupply }} <br>-->
      地址： {{ addr }} <br>
      余额： {{ balanceOf }} LARK<br><br>

      <b-button variant="outline-primary" @click="beforetransfer">转帐</b-button>
      <div v-if="transferFlag">
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="地址" v-model="to"
        ></b-input>
        <b-input
                class="mb-2 mr-sm-2 mb-sm-0 user"
                placeholder="金额" v-model="value"
        ></b-input>
        <div class="loginbtn">
          <b-button variant="outline-primary" @click="transfer">确认转帐</b-button>
        </div>
      </div>

    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Login",
    data() {
      return {
        nickname:'',
        addr: '',
        balanceOf: '',
        password:'',
        isLoading: false,
        flag: true,
        totalSupply: '',
        to:'',
        value:'',
        transferFlag: false
      }
    },
    methods:{
      beforetransfer(){
        this.transferFlag = !this.transferFlag
      },
      transfer(){
        this.isLoading = true
        this.transferFlag = false
        let _this = this
        // let value3 =  this.web3.utils.toBN(value).toString()
        let value3 = this.web3.utils.toWei(this.value, 'ether')
        // console.log(566, value3)

        let addr = this.$store.state.addr
        let instance = this.$store.state.walletinstance
        // console.log(23567, instance )
        async  function main(){
          // console.log(31111, instance)
          let s = await instance.transfer(_this.to, value3, {from: addr})
          // console.log(66, s)
          _this.isLoading = false
          //直接刷新当前页面
          _this.$router.go(0)
        }
        main()


      },

      dealNumber (money){
        /*将100000转为100,000.00形式*/
        if(money && money!=null){
          money = String(money)
          let left=money.split('.')[0],right=money.split('.')[1];
          right = right ? (right.length>=2 ? '.'+right.substr(0,2) : '.'+right+'0') : '.00'
          let temp = left.split('').reverse().join('').match(/(\d{1,3})/g)
          return (Number(money)<0?"-":"") + temp.join(',').split('').reverse().join('')+right
        }else if(money===0){
          //注意===在这里的使用，如果传入的money为0,if中会将其判定为boolean类型，故而要另外做===判断
          return '0.00'
        }else{
          return ""
        }
      }

      },

      components: {
        Loading
      },
      mounted() {
        let BN = this.web3.utils.BN
        // console.log(111, BN)

        let that = this
        let addr = this.addr = this.$store.state.addr

        // console.log(1111, this.msg.sender)
        async  function main(){
          let res = await that.axios.get('LarkToken.json')
          // console.log(667, res.data)
          let contract = that.TruffleContract(res.data)
          // console.log(77, contract)
          contract.setProvider(that.web3Provider)
          // contract.setProvider(that.web3.currentProvider)
          let instance = await contract.deployed()

          // console.log(31111, instance)
          // console.log(1222, instance.msg.sender)
          //将得到的数据存入vuex中
          that.$store.commit('saveWalletInstance', instance)

          let a = await instance.balanceOf(addr) * 1e-18
          that.balanceOf = that.dealNumber(a)

          // that.balanceOf = that.web3.utils.fromWei(a, 'ether')


          let b = await instance.totalSupply() * 1e-18
          that.totalSupply = that.dealNumber(b)
          // console.log(999, that.balanceOf)

          // that.posts = posts
          that.isLoading = false
          //将得到的数据存入vuex中
          // that.$store.commit('saveHot', posts.reverse())
        }
        main()
      },


    }
</script>

<style scoped>

  .login{
    margin: 4rem 4rem 18rem 14rem;

  }
  .user{
    margin-top: 1rem;
    width: 50%;
  }
  .loginbtn{
    margin-top: 1.5rem;
  }
