<script>
  import Loading from './Loading'
    export default {
        name: "Post",
      data() {
        return {
          db:{},
          body: '',
          title: '',
          keywords: '',
          isLoading: false,
          flag: false
        }
      },

      methods: {
        post(){
            this.flag = false
            this.isLoading = true
            let parentAuthor = ''
            let parentPermlink = 'etherjiang'
            // let parentPermlink = this.tags.trim().split(/\s+/)[0]
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.instance
            let addr = this.$store.state.addr
            let permlink = this.getstr()
            let title = this.title
            let body = this.body
            let keywords = this.keywords.trim().split(/\s+/)
            // console.log(11111,keywords)
            // console.log(11122,JSON.stringify(keywords))
