            let that = this

            //发布到本地数据库
            //把数据拼成一个JSON
            // let post = {
            //   "title": title,
            //   "author": author,
            //   "category": 'hive-180932',
            //   "permlink": permlink,
            //   "body": body,
            //   "created": new Date().toISOString().split('.')[0],
            //   "json_metadata": JSON.stringify(jsonMetadata)
            // }
          // let db = this.$store.state.db
          // let tableName = this.$store.state.username

          async function main(){
            // await that.saveOneToDb(post, db, tableName)
            let post2 = {
              "title": title,
              "nickname": nickname,
              "addr": addr,
              "category": 'etherjiang',
              "permlink": permlink,
              "body": body,
              "created": new Date().toISOString().split('.')[0],
              "json_metadata": jsonMetadata
            }

            //上传文本到ipfs
            const content = Buffer.from(JSON.stringify(post2))
            let res = await that.ipfs.add(content)
            // console.log(566, res)
            // console.log(5668, res[0].hash)

            let ss = await instance.set(res[0].hash, title, JSON.stringify(keywords), {from: addr})
            console.log(955, ss)

            //清空vuex中的数据
            that.$store.commit('saveHot', [])
