  .article{
    margin-top: 3rem;
  }
  .posttime{
    font-size: 0.8rem;
    display: inline;
  }
  .content{
    color: rgba(55,53,58,0.75);
    align: center;
    margin: 1rem auto;
    height: 3rem;
    width: 70%;
    border-bottom: 1px solid #ddd;
  }
  img {
    vertical-align: middle;
    width: 2rem !important;
    background-color: blueviolet;
  }

  a{
    color: rgba(55,53,58,0.75);
  }

  a:hover{
    color: darkcyan;
    text-decoration: none;
  }

  .mt-3{
    width: 75%;
    margin: 1rem auto !important;
  }
  .search{
    margin: 1rem auto !important;
    width: 75%;
  }



</style>
