<template>
  <div class="article">
    <b-input
      id="inline-form-input-name"
      class="mb-2 mr-sm-2 mb-sm-0 search"
      placeholder="搜索" v-model="keywords"
    ></b-input>

    <div class="content" v-for="post in search(keywords)">

      <router-link :to="'/trend/'+post.addr+'/'+post.permlink">
        <b-img :src="post.body | takeImage" fluid  class="courseimg"></b-img>
      </router-link>

      <div class="title">
        <router-link :to="'/trend/'+post.addr+'/'+post.permlink">
          <span style="font-size: 1.2rem">{{ post.title }}</span><br>
        </router-link>
        <p class="posttime">@{{ post.nickname }} ({{ post.addr | formatAddr }})　
        <span style="margin-left: 1rem">{{ post.created  | formatTime}}</span></p>
      </div>
      <p style="padding: 0 1rem 1rem 1rem">{{ post.body | abstractFn }} ......</p>

      <!--<div style="clear:both;"></div>-->


    </div>

      <!--加载动画-->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

    <!--<div class="btn">-->
      <!--&lt;!&ndash;<b-button variant="outline-primary" @click="getNext">查看更多</b-button>&ndash;&gt;-->
    <!--</div>-->
  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Index",
      data(){
          return{
            keywords:'',
            posts:[],
            author:'',
            permlink:'',
            isLoading: true
          }
      },
      methods:{
        search(keywords){
          let posts = this.$store.state.hot
          return posts.filter(item => {
            if(item.title.includes(keywords)){
              // console.log(166, '查到了！')
              return item
            }
          })
        },

        getHot(){
            let that = this
            let query = { tag: "hive-180932", limit : 20 }
            this.steem.api.getDiscussionsByTrending(query, function(err, data) {
              that.posts = data
              //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
              that.author = data[data.length - 1].author
              that.permlink = data[data.length - 1].permlink
              //将得到的数据存入vuex中
              that.$store.commit('saveHot', data)
              that.isLoading = false
            })
          },

        getNext(){
          let that = this
          that.isLoading = true
          let query = {tag: "hive-180932", limit: 20, start_author: this.author, start_permlink: this.permlink}
          this.steem.api.getDiscussionsByTrending(query, function(err, data) {
            data.forEach(post => {
              if (post.permlink != that.permlink) {
                that.posts.push(post)
              }
            })
            //取到最后一篇文章的author和Permlink，做为下一次查询的起始点
            that.author = data[data.length - 1].author
            that.permlink = data[data.length - 1].permlink
            //将得到的数据存入vuex中
            that.$store.commit('saveHot', that.posts)
            that.isLoading = false
            if(data.length == 1){
              alert('没有更多了！')
            }
          })
        },

        utf8ArrayToStr(array) {
              let out, i, len, c
              let char2, char3
              out = ""
              len = array.length
              i = 0
              while(i < len) {
                  c = array[i++]
                  switch(c >> 4)
                  {
                      case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
                      // 0xxxxxxx
                      out += String.fromCharCode(c)
                      break
                      case 12: case 13:
                      // 110x xxxx   10xx xxxx
                      char2 = array[i++]
                      out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F))
                      break
                      case 14:
                          // 1110 xxxx  10xx xxxx  10xx xxxx
                          char2 = array[i++];
                          char3 = array[i++];
                          out += String.fromCharCode(((c & 0x0F) << 12) |
                              ((char2 & 0x3F) << 6) |
                              ((char3 & 0x3F) << 0))
                          break
                  }
              }

              return out
          },
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.instance
          // console.log(1111, instance)
        async  function main(){
            if(that.$store.state.hot == false){
                console.log(1112, "从网络取值！")
                if(Object.keys(instance).length === 0) {
                    //如果刷新, instance未定义
                    // console.log(777777, "instance未定义")
                    let res = await that.axios.get('Post.json')
                    // console.log(667, res.data)
                    let contract = that.TruffleContract(res.data)
                    // console.log(77, contract)
                    contract.setProvider(that.web3Provider)
                    // contract.setProvider(that.web3.currentProvider)
                    let instance = await contract.deployed()
                    // console.log(31111, instance)
                    //将得到的数据存入vuex中
                    that.$store.commit('saveInstance', instance)

                    let s = await instance.id()
                    console.log(555, s)
                    // console.log(566, s.words[0])

                    let posts = []
                    for(let i=1; i <= s ; i++){
                    // for(let i=1; i <= s.words[0] ; i++){
                        let s1 = await instance.get(i)
                        console.log(56000, s1[0]) //contenthash
                        // console.log(56111, s1[1])  //title
                        // console.log(56222, JSON.parse(s1[2]))  //keywords
                        let con = await that.ipfs.cat(s1[0])
                        let t = that.utf8ArrayToStr(con)
                        posts.push(JSON.parse(t))
                    }
                    console.log(899, posts)
                    // that.posts = posts
                    that.isLoading = false
                    //将得到的数据存入vuex中
                    that.$store.commit('saveHot', posts.reverse())
                }  else{
                    //instance已定义好
                    // console.log(88888, "instance已定义")
                    let s = await instance.id()
                    let posts = []
                    for(let i=1; i <= s.words[0] ; i++){
                        let s1 = await instance.get(i)
                        // console.log(56000, s1[0]) //contenthash
                        // console.log(56111, s1[1])  //title
                        // console.log(56222, JSON.parse(s1[2]))  //keywords
                        let con = await that.ipfs.cat(s1[0])
                        let t = that.utf8ArrayToStr(con)
                        posts.push(JSON.parse(t))
                    }
                    console.log(7799, posts)
                    that.posts = posts
                    that.isLoading = false
                    //将得到的数据存入vuex中
                    that.$store.commit('saveHot', posts)
                }
            }else{
                that.isLoading = false
            }
        }
        main()
      },

      filters:{
        formatTime(data){
          return data.substring(0,10)
        },
        formatAddr(data){
          return data.substring(0,6)+'...'+data.substring(38)
        },
        takeImage(text){
          let reg = /!\[(.*?)\]\((.*?)\)/
          // let reg = /\!\[.*?\)/
          let a = text.match(reg)
          if(a === null){
            return
          }
          return a[2]
        },
        abstractFn(res){
          if(!res){
            return ''
          }else{
            let str=res.replace(/(\*\*|__)(.*?)(\*\*|__)/g,'')          //全局匹配内粗体
                    .replace(/\!\[[\s\S]*?\]\([\s\S]*?\)/g,'')           //全局匹配图片
                    .replace(/\([\s\S]*?\)/g,'')                              //全局匹配连接
                    .replace(/<\/?.+?\/?>/g,'')                             //全局匹配内html标签
                    .replace(/http.*\w/g,'')                              //全局http连接
                    .replace(/(\*)(.*?)(\*)/g,'')                               //全局匹配内联代码块
                    .replace(/`{1,2}[^`](.*?)`{1,2}/g,'')                       //全局匹配内联代码块
                    .replace(/```([\s\S]*?)```[\s]*/g,'')                       //全局匹配代码块
                    .replace(/\~\~(.*?)\~\~/g,'')                               //全局匹配删除线
                    .replace(/[\s]*[-\*\+]+(.*)/g,'')                           //全局匹配无序列表
                    .replace(/[\s]*[0-9]+\.(.*)/g,'')                           //全局匹配有序列表
                    .replace(/(#+)(.*)/g,'')                                    //全局匹配标题
                    .replace(/(>+)(.*)/g,'')                                    //全局匹配摘要
                    .replace(/\r\n/g,"")                                        //全局匹配换行
                    .replace(/\n/g,"")                                          //全局匹配换行
            // .replace(/\s/g,"")                                          //全局匹配空字符;
            return str.slice(0,180)
          }
        }

      },
    }
</script>

