    /*background-color: rgba(58,46,52,0.04);*/
  }
  .title{
    text-align: center;
    width: 95%;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }

  .courseimg{
    display: block;
    margin: 0 auto;
  }
  a{
    color: rgba(55,53,58,0.75);
  }

  a:hover{
    color: darkcyan;
