import Vue from 'vue'
import Vuex from 'vuex'
import Cookie from 'vue-cookies'

Vue.use(Vuex)

export default new Vuex.Store({

  // 组件中通过 this.$store.state.username 调用
  state: {
    nickname: Cookie.get("nickname"),
    addr: Cookie.get("addr"),
    posts:[],
    db:{},
    instance:{},
    walletinstance:{},
    feed:[],
    hot:[],
    collections:[],
    searchPosts:[],
    repliestoauthor:[]

  },
  mutations: {
    // 组件中通过 this.$store.commit('saveToken',参数)  调用,只能带一个参数，有多个参数以对象的形式传入
    saveUser: function (state, userobj) {
      state.nickname = userobj.nickname
      state.addr = userobj.addr

      Cookie.set("nickname", userobj.nickname, "1d")
      Cookie.set("addr", userobj.addr, "1d")

    },
    savePosts: function (state,  posts) {
      state.posts = posts
    },

    saveRepliesToAuthor: function (state,  repliestoauthor) {
      state.repliestoauthor = repliestoauthor
    },

    saveFeed: function (state,  feed) {
      state.feed = feed
    },

    saveHot: function (state,  hot) {
      state.hot= hot
    },

    saveCollections: function (state,  collections) {
      state.collections= collections
    },

    saveSearchPosts: function (state,  searchPosts) {
      state.searchPosts= searchPosts
    },

    saveDB: function (state, db){
      state.db = db
    },

    saveInstance: function (state, instance){
      state.instance = instance
    },

    saveWalletInstance: function (state, instance){
      state.walletinstance = instance
    },


    clearUser: function (state) {
      state.nickname = null
      state.addr = null
      Cookie.remove('nickname')
      Cookie.remove('addr')
    }
  }
})

