<template>
  <div class="login">
    <h2>用户登录</h2>
    <div v-if="flag">
      <b-input
              id="inline-form-input-name"
              class="mb-2 mr-sm-2 mb-sm-0 user"
              placeholder="设置呢称(任意长度的任意字符，不能为空)" v-model="nickname"
      ></b-input>
      <br>
      地址： {{ addr }}

      <div class="loginbtn">
        <b-button variant="outline-primary" @click="login">登录</b-button>
      </div>
    </div>
    <!--加载动画-->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Login",
    data() {
      return {
        nickname:'',
        addr: '',
        password:'',
        isLoading: false,
        flag: true
      }
    },
    methods:{
      login(){
        //用私钥或密码登录
        this.flag = false
        // this.isLoading = true
        if(this.nickname !== '' && this.addr !== ''){
          this.$store.commit('saveUser', {nickname: this.nickname, addr: this.addr})
          this.$router.push({path:'/'})
        }else{
          alert("呢称或地址不能为空！")
          this.flag = true
        }
      }
    },

    components: {
      Loading
    },
    mounted() {
      let that = this
      async function main(){
        let accounts = await window.ethereum.enable()
        // console.log(11111, accounts)
        that.addr = accounts[0]
        console.log(124, addr)
      }
      main()
    }


  }
</script>

