<template>
  <div class="article">
    <button @click="getEventWeb3">获取日志</button>
    <br>
    <button @click="getSign">获取签名</button>
    <br>
    <b-input
            class="mb-2 mr-sm-2 mb-sm-0 user"
            placeholder="转帐地址" v-model="to"
    ></b-input>

    <b-input
            class="mb-2 mr-sm-2 mb-sm-0 user"
            placeholder="eth" v-model="value" eth
    ></b-input>
    <button @click="sendTransaction">转帐</button>

  </div>
