<script>

  export default {
    name: "Index",
    data(){
      return{
        to: '',
        value: ''
      }
    },
    methods:{

      getEthLogs(){
        let _this = this
        async function main(){
          // let blockNumber = await _this.ethapi.proxy.eth_getBlockByNumber('0x10d4f')
          // console.log(123, blockNumber)
          console.log(888, _this.ethapi)
          let logs = await _this.ethapi.log.getLogs('0x98C59036C71Ef10684b3A4a3ffA85b280fe7b402', 7745600,  'latest')
          // let logs = await _this.ethapi.log.getLogs({address: '0x98C59036C71Ef10684b3A4a3ffA85b280fe7b402', fromBlock: 7745600, toBlock: 'latest'})
          console.log(2263, logs)

        }

        main()
      },

      getEventWeb3(){
        let _this = this
        console.log(566, this.web3)
        console.log(567, this.web3.eth)
        console.log(568, this.web3.eth.accounts)
        let instance = this.$store.state.instance
        async function main(){
          let ttt = await instance.getPastEvents('SetContent', {fromBlock: 0, toBlock: 'latest'})
          // let ttt = await instance.getPastEvents('SetContent', {fromBlock: 7745600, toBlock: 'latest'})
          console.log(8855, ttt)
          // console.log(8856, ttt[0].raw.data) //日志数据（16进制）
          console.log(8856, ttt[0].returnValues[0])
          // console.log(8857, _this.web3.utils.hexToUtf8(ttt[0].raw.data)) //16进制转成utf8
          // console.log(8857, _this.web3.utils.hexToUtf8(ttt[0].raw.data)) //16进制转成utf8
        }
        main()
      },

      getSign(){
        /// 先计算一个hash
        let hash = this.web3.utils.sha3("message to sign")
        // console.log(156, hash)
        let addr = this.$store.state.addr
        let _this = this

        async function main() {
          let res = await _this.web3.eth.personal.sign(hash, addr)
          console.log(333, res)
        }
        // main()

        // recipient 表示向谁付款.
        // amount, 单位 wei, 指定发送金额数量.
        // nonce 保护重放攻击
        // contractAddress 保护跨合约重放攻击
        let recipient = '0xCD7324469cC6d4DF5BfdaEaDBCDDb008866F0913'
        let amount = 400000000000000
        let nonce = 12
        let contractAddress = '0xD952e0c02F5D6D77c0207daAb436F3540a99B511'
        async function signPayment() {
          console.log(899, recipient)
          let hash2 = "0x" + _this.abi.soliditySHA3(
                  ["address", "uint256", "uint256", "address"],
                  [recipient, amount, nonce, contractAddress]
          ).toString("hex")
          console.log(566, hash2)

          let res2 = await _this.web3.eth.personal.sign(hash2, addr)
          console.log(699, res2)

        }
        signPayment()

      },

      sendTransaction(){
        let _this = this
        console.log(456, this.value * 1e18)
        let addr = this.$store.state.addr
        async  function main() {
          let res = await _this.web3.eth.sendTransaction({from: addr, to:_this.to, value: _this.value* 1e18})
          console.log(899, res)
        }
        main()
      }

    },

    components: {

    },


  }
</script>

