//设置ipfs节点, ,挂载到全局
const ipfs = ipfsClient({ host: 'steemjiang.com', port: '9001', protocol: 'https' })
Vue.prototype.ipfs = ipfs

const ethapi = require('etherscan-api').init('DYX36PF9I32G7TDQDFF97CSK24SXWZXIN9','ropsten',  '3000')


Vue.prototype.getstr = getstr
Vue.prototype.imgAdd = imgAdd
Vue.prototype.initDatabase = initDatabase
Vue.prototype.getAllPosts = getAllPosts
Vue.prototype.getAllReplies = getAllReplies
Vue.prototype.getCollections = getCollections
Vue.prototype.getRepliesToAuthor = getRepliesToAuthor
Vue.prototype.saveOneToDb = saveOneToDb
Vue.prototype.TruffleContract = TruffleContract
Vue.prototype.web3 = web3
Vue.prototype.web3Provider= web3Provider
Vue.prototype.axios  = axios
Vue.prototype.ethapi  = ethapi
Vue.prototype.abi  = abi

Vue.config.productionTip = false


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

router.beforeEach(function (to, from, next) {
  if(to.meta.requireAuth){
    // 要去的url只有登陆成功后才能访问
    if (store.state.nickname) {
      next()
    } else {
      next({name: 'login'})
    }
  }else{
    next()
  }
})

